<template>
    <v-dialog
        v-model="dialog"
        width="800px"
    >
        <v-card>
            <v-card-title class="primary white--text">
                
            </v-card-title>
            <v-container>
                <v-stepper v-model="e1">
                    <v-stepper-header>
                        <v-stepper-step :complete="e1 > 1" step="1"></v-stepper-step>

                        <v-divider />

                        <v-stepper-step :complete="e1 > 2" step="2"></v-stepper-step>

                        <v-divider />

                        <v-stepper-step step="3">!</v-stepper-step>
                    </v-stepper-header>

                    <v-stepper-items>
                        <v-form lazyValidation>
                            <v-stepper-content step="1">
                                <v-row class="mx-2">
                                    <v-col
                                        class="align-center justify-space-between"
                                        cols="12"
                                        md="6"
                                    >
                                        <v-select
                                            v-model="input.material_id"
                                            :items="items.order"
                                            label=""
                                            outlined
                                            dense
                                            required
                                            v-bind="material_id_disable ? {['disabled']: true} : ''"
                                        />
                                    </v-col>
                                    <v-col cols="12" md="6">
                                        <v-text-field
                                            v-model="input.material_name"
                                            :counter="30"
                                            label=""
                                            required
                                        />
                                    </v-col>
                                    <v-col cols="12" md="6">
                                        <v-text-field
                                            v-model="input.material_source"
                                            :counter="30"
                                            label=""
                                            required
                                        />
                                    </v-col>
                                    <v-col cols="12" md="6">
                                        <v-select
                                            v-model="input.material_origin"
                                            :items="items.origin"
                                            label=""
                                            outlined
                                            dense
                                            required
                                        />
                                    </v-col>
                                    <v-col cols="6">
                                        <v-text-field
                                            v-model="input.material_spec_1"
                                            :counter="30"
                                            label="1"
                                            required
                                        />
                                    </v-col>
                                    <v-col cols="6">
                                        <v-combobox
                                            v-model="input.material_spec_2"
                                            :items="items.spec_2"
                                            label="2"
                                            required
                                            multiple
                                            outlined
                                            smallChips
                                            dense
                                        />
                                    </v-col>
                                    <v-col class="d-flex justify-end">
                                        <v-btn color="grey light-4 mr-4"
                                               text
                                               large
                                               @click="dialog = false"
                                        >
                                            
                                        </v-btn>
                                        <v-btn class="mx-1"
                                               color="primary"
                                               @click="e1 = 2"
                                               large
                                        >
                                            
                                            <v-icon>mdi-chevron-right</v-icon>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </v-stepper-content>

                            <v-stepper-content step="2">
                                <v-row class="mx-2">
                                    <v-col
                                        class="align-center justify-space-between"
                                        cols="12"
                                        md="6"
                                    >
                                        <v-text-field
                                            v-model="input.material_patent"
                                            :counter="30"
                                            label=""
                                        />
                                    </v-col>
                                    <v-col cols="12" md="6">
                                        <v-select
                                            v-model="input.material_certification"
                                            :items="items.certification"
                                            label=""
                                            multiple
                                            outlined
                                            smallChips
                                            dense
                                        />
                                    </v-col>
                                    <v-col
                                        class="align-center justify-space-between"
                                        cols="12"
                                        md="6"
                                    >
                                        <v-text-field
                                            v-model="input.material_clinic"
                                            :counter="100"
                                            label="/"
                                        />
                                    </v-col>
                                    <v-col
                                        class="align-center justify-space-between"
                                        cols="12"
                                        md="6"
                                    >
                                        <v-combobox
                                            v-model="input.material_fn"
                                            :items="items.fn"
                                            label=""
                                            multiple
                                            outlined
                                            smallChips
                                            dense
                                        />
                                    </v-col>
                                    <v-col class="d-flex justify-end">
                                        <v-btn color="grey light-4 mr-4"
                                               text
                                               @click="dialog = false"
                                               large
                                        >
                                            
                                        </v-btn>
                                        <v-btn class="mx-1"
                                               color="primary lighten-2"
                                               @click="e1 = 1"
                                               outlined
                                               large
                                        >
                                            <v-icon>mdi-chevron-left</v-icon>
                                            
                                        </v-btn>
                                        <v-btn class="mx-1"
                                               color="primary"
                                               @click="e1 = 3"
                                               large
                                        >
                                            
                                            <v-icon>mdi-chevron-right</v-icon>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </v-stepper-content>

                            <v-stepper-content step="3">
                                <div class="headline text-center">
                                    
                                </div>

                                <div class="body-1 grey--text mb-4 text-center">
                                     5 
                                </div>

                                <v-btn
                                    block
                                    color="primary"
                                    @click="dialog = false"
                                    large
                                >
                                    
                                </v-btn>
                            </v-stepper-content>
                        </v-form>
                    </v-stepper-items>
                </v-stepper>
            </v-container>
            <!--
            <v-card-actions>
                <v-btn
                    text
                    color="primary"
                >
                    More
                </v-btn>
                <v-spacer />
                <v-btn
                    text
                    color="primary"
                    @click="dialog = false"
                >
                    Cancel
                </v-btn>
                <v-btn
                    text
                    @click="dialog = false"
                >
                    Save
                </v-btn>
            </v-card-actions>
            -->
        </v-card>
    </v-dialog>
</template>

<script>
import bus from '@/bus';

export default {
    data() {
        return {
            dialog: false,
            e1: 1,
            input: {
                material_id: '',
                material_name: '',
                material_source: '',
                material_spec_1: '',
                material_spec_2: '',
                material_origin: '',
                material_patent: '',
                material_certification: '',
                material_clinic: '',
                material_fn: ''
            },
            material_id_disable: false,
            items: {
                order: [
                    {
                        text: 'R001  ()',
                        value: 'R001'
                    },
                    {
                        text: 'R002 /',
                        value: 'R002'
                    },
                    {
                        text: 'R003 ()',
                        value: 'R003'
                    },
                    {
                        text: 'R029 ',
                        value: 'R029'
                    }
                ],
                location: [
                    'Item 1',
                    'Item 2',
                    'Item 3',
                    'Item 4'
                ],
                origin: [
                    '',
                    '',
                    '',
                    '',
                    ''
                ],
                // type: [
                //     '',
                //     '',
                //     '',
                //     '',
                //     '',
                //     ''
                // ],
                spec_1: '',
                spec_2: [
                    '',
                    '',
                    '',
                    '',
                    '',
                    ''
                ],
                certification: [
                    'Kosher', 'Halal', 'FSSC', 'MSC', 'GMP', 'NGMO', 'GRAS', 'HACCP', 'PDR', 'JECFA', 'ESCOP', 'BHMA', 'CE', 'ISO', '', ''
                ],
                fn: [
                    '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '/', '', ''
                ]
            }
        };
    },
    watch: {
        e1(val) {
            if (val === 3) {
                setTimeout(() => {
                    this.dialog = false;
                    this.e1 = 1;
                }, 5000);
            }
        }
    },
    mounted() {
        bus.$on('addMaterial', (material_id) => {
            this.dialog = !this.dialog;

            if (material_id !== undefined && material_id !== '') {
                this.input.material_id = material_id;
                this.material_id_disable = true;
            } else {
                this.input.material_id = '';
                this.material_id_disable = false;
            }
        });
    }
};
</script>
